@import url(https://fonts.googleapis.com/css?family=Righteous);
body {
  margin: 0;
  padding: 0;
  background: whitesmoke;
  /* background: linear-gradient(
    10deg,
    rgba(115, 177, 231, 1) 0%,
    rgba(10, 119, 213, 1) 50%,
    rgba(83, 159, 225, 1) 100%
  ); */
  min-height: 100vh;
  font-size: 16px;
  font-family: Righteous, cursive;
}

.Particles {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}

.Navigation {
  display: flex;
  justify-content: flex-end;
  box-shadow: 10px 10px 41px 3px rgba(0, 0, 0, 0.23);
  background: -webkit-linear-gradient(
    60deg,
    rgba(255, 255, 255, 0.06),
    rgba(255, 255, 255, 0.25),
    rgba(255, 255, 255, 0.08),
    rgba(255, 255, 255, 0.03)
  );
  background: linear-gradient(
    30deg,
    rgba(255, 255, 255, 0.06),
    rgba(255, 255, 255, 0.25),
    rgba(255, 255, 255, 0.08),
    rgba(255, 255, 255, 0.03)
  );
  box-sizing: border-box;
  padding: 0px 15px;
}

.Navigation p {
  font-size: 1.2em;
  cursor: pointer;
  background: #ec173a;
  color: whitesmoke;
  padding: 10px;
  border-radius: 10px;
  margin: 8px;
  -webkit-transition: -webkit-transform 0.25s ease-out;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.Navigation p:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.Logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}

.Tilt {
  box-shadow: 0px 0px 41px 3px rgba(0, 0, 0, 0.23);
  background: -webkit-linear-gradient(
    60deg,
    rgba(0, 0, 0, 0.06),
    rgba(0, 0, 0, 0.01),
    rgba(0, 0, 0, 0.08),
    rgba(0, 0, 0, 0.03)
  );
  background: linear-gradient(
    30deg,
    rgba(0, 0, 0, 0.06),
    rgba(0, 0, 0, 0.01),
    rgba(0, 0, 0, 0.08),
    rgba(0, 0, 0, 0.03)
  );
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 24px;
  border: 20px solid transparent;
  box-sizing: border-box;
}

.Tilt img {
  width: 80%;
  -webkit-animation: eyeRotate 18s infinite linear;
          animation: eyeRotate 18s infinite linear;
}

.Tilt img:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

@-webkit-keyframes eyeRotate {
  0% {
    -webkit-transform: rotate(0) scale(1);
            transform: rotate(0) scale(1);
  }

  50% {
    -webkit-transform: rotate(360deg) scale(0.75);
            transform: rotate(360deg) scale(0.75);
  }

  100% {
    -webkit-transform: rotate(720deg) scale(1);
            transform: rotate(720deg) scale(1);
  }
}

@keyframes eyeRotate {
  0% {
    -webkit-transform: rotate(0) scale(1);
            transform: rotate(0) scale(1);
  }

  50% {
    -webkit-transform: rotate(360deg) scale(0.75);
            transform: rotate(360deg) scale(0.75);
  }

  100% {
    -webkit-transform: rotate(720deg) scale(1);
            transform: rotate(720deg) scale(1);
  }
}

.ImageLinkForm p {
  text-align: center;
  font-size: 1.8rem;
  margin: 25px;
}

.imageInput {
  width: 90%;
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  padding: 15px;
  background: -webkit-linear-gradient(315deg, #eceddc 25%, transparent 25%) -50px 0,
    -webkit-linear-gradient(225deg, #eceddc 25%, transparent 25%) -50px 0,
    -webkit-linear-gradient(135deg, #eceddc 25%, transparent 25%),
    -webkit-linear-gradient(45deg, #eceddc 25%, transparent 25%);
  background: linear-gradient(135deg, #eceddc 25%, transparent 25%) -50px 0,
    linear-gradient(225deg, #eceddc 25%, transparent 25%) -50px 0,
    linear-gradient(315deg, #eceddc 25%, transparent 25%),
    linear-gradient(45deg, #eceddc 25%, transparent 25%);
  background-size: 100px 8px;
  background-color: #ec173a;
  border-radius: 100px;
  box-shadow: 10px 10px 41px 3px rgba(0, 0, 0, 0.23);
}

.imageInput input {
  font-size: 1.2rem;
  line-height: 2rem;
  border: 2px solid transparent;
  outline: 2px solid transparent;
  border-radius: 20px 0px 0px 20px;
  background: black;
  color: whitesmoke;
  text-align: center;
  font-family: Righteous, cursive;
  outline: none;
  width: 60%;
  padding: 10px 25px;
  box-sizing: border-box;
}

.imageInput button {
  border: 2px solid transparent;
  outline: 2px solid transparent;
  border-radius: 0px 20px 20px 0px;
  width: 25%;
  font-size: 1.2em;
  font-family: Righteous, cursive;
  box-sizing: border-box;
  background: #ec173a;
  color: whitesmoke;
  cursor: pointer;
}

.imageInput input:focus {
  border: 2px solid #ec173a;
  border-right: none;
  outline: none;
}

.imageInput input:focus + button {
  border: 2px solid #ec173a;
  border-left: 2px solid transparent;
  outline: none;
}

.SignIn {
